* {
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
  width: 100%;
  color: #2f1343;
  font-family: 'Barlow', sans-serif;
  /* overflow-x: hidden; */
}

h2 {
  font-family: 'ozwald', sans-serif;
}

img {
  width: 100%;
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  top: 0;
  height: 100%;
  padding: 25vh 2em;
  /* font-size: .75em; */
  z-index: 1;
}

.page {
  width: 100%;
  min-height: 100%;
  padding: 2em 2em 2em 5em;
}

/* .page.show {
  height: auto;
} */
.upper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.upper img, .sideBySide img {
  width: 50%;
  max-height: 80%;
  margin: 0 1em;
}

.upper div, .textBox {
  margin: 0 1em;
}

.flex {
  display: flex;
  align-self: center;
  justify-content: center;
  width: 100%;
}

.flex img {
  height: 50vh;
  width: 24vh;
  /* THIS IS SO DUMB WTF */
  margin: 1em;
}

.sideBySide {
  display: flex;
  align-items: center;
}

.right {
  flex-flow: row-reverse;
}

.imgRow {
  display: flex;
  position: relative;
  left: -6em;
  width: 99vw;
}

.imgRow img {
  width: 10%;
  flex: 1 1 0;
}

.text {
  display: flex;
  flex-direction: column;
}

.reverse {
  flex-direction: row-reverse;
}

.fa-arrow-down {
  margin-top: 1em;
}

.reverse .fa-arrow-down {
  align-self: flex-end;
  margin-right: .25em;
}

.lower {
  display: flex;
  flex-direction: column;
  padding-left: 1em;
  padding: 0 3em 0 1em;
}

#graph {
  background: white;
}

.fa-arrow-up {
  align-self: center;
}